.root {
}

.root :global(.react-multiple-carousel__arrow) {
    display: block;
    width: 114px;
    height: 114px;
    background: none;
    background: green;
}

.root :global(.react-multiple-carousel__arrow::before) {
    content: '';
}

.root :global(.react-multi-carousel-track) {
    align-items: center;
    align-content: center;
}

.item {
    user-select: none;
    padding: 40px 0;
}

.itemCurrent {
    position: relative;
    z-index: 10;
}

.item .img {
    margin: 0 auto;
    display: block;
    min-width: 310px;
    min-height: 429px;
    max-width: 310px;
    max-height: 429px;
    transform: scale(0.8, 0.8) rotate(0deg);
    opacity: 0.4;
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
}

.itemCurrent .img {
    transform: scale(1.2, 1.2) rotate(0deg);
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
    opacity: 1;
}

.itemCurrentPrev .img {
    transform: rotate(-7deg);
}

.itemCurrentNext .img {
    transform: rotate(7deg);
}

.itemCurrentPrev .img,
.itemCurrentNext .img {
    transition: transform 0.3s ease-out;
}

.link {
    background: var(--c-white);
}